@import url(https://fonts.googleapis.com/css2?family=Exo+2:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Outfit:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
/* Global CSS Classes here */


html,body {
  margin: 0;
}

/* Fonts used */

.font-roboto {
  font-family: 'Roboto', sans-serif;
}

.font-outfit {
  font-family: 'Outfit', sans-serif;
}

.font-exo {
  font-family: 'Exo 2', sans-serif;
}

.web-title {
  font-size: 45px;
  letter-spacing: 2px;
  color: #f0ad4e;
}

.web-hr {
  width: 80%;
}

.web-p {
  text-align: justify;
  font-size: 18px;
}

/* Website background */

.site-background-color {
  background-color: #211522;
}

/* Navbar and Footer*/

.background-color {
  background-color: #49274A;
}

.circle-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.background-color .navbar-brand {
  color: #FAFAFA;
  letter-spacing: 0.09em; 
}

.background-color .navbar-brand:hover,
.background-color .navbar-brand:focus {
    color: #F8EEE7;
}

.background-color .navbar-nav .nav-link {
  color: #FAFAFA;
} 

.background-color .navbar-nav .nav-item .nav-link.active,
.background-color .navbar-nav .nav-item .nav-link:hover,
.background-color .navbar-nav .nav-item .nav-link:focus,
.background-color .navbar-nav .nav-item .nav-link:active{
  color: #FFFFFF;
  background-color: #94618E;
  border-radius: .3rem;
}

footer {
  min-height: 50px;
}

footer p {
  font-size: 14px;
}

#footer-icons a {
  color: #FAFAFA
}

#footer-icons a:hover,
#footer-icons a:focus {
  color: #f0bae9;
  animation: rubberBand; 
  animation-duration: 2s; 
}

/* ScrollButton */

.scroll-button {
  height: 42px;
  width: 42px;
  position: fixed;
  bottom: 10px;
  right: 10px;
  background-color: transparent;
}

.scroll-button:focus {
  background-color: transparent;
} 

.scroll-button:hover {
  background-color: rgba(34, 0, 40, 0.6);
}

/* Media Queries */

@media screen and (max-width: 350px) {
  .background-color .navbar-brand {
    color: #FAFAFA;
    font-size: 20px;
    letter-spacing: 0.09em; 
  }
}

@media screen and (max-width: 450px) {
  .web-title {
    font-size: 34px;
    letter-spacing: 2px;
    color: #f0ad4e;
  }

  .web-hr {
    width: 95%;
  }

  .web-p {
    text-align: justify;
    font-size: 16px;
  }
}
/* Cover image */

.welcome-img-container {
    height: 100vh;
    min-height: 600px;

    background: url(/my-portfolio/static/media/mainCoverImg.bcbb4c91.jpg) no-repeat center center fixed;
    background-size: cover;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
}

/* Jumbotron */

.jumbotron {
    background-color: rgba(34, 0, 40, 0.6);
    color: #FFFFFF;
    border-radius: .3rem;
}

.jumbotron h1 {
    font-size: 45px;
}

.jumbotron h2 {
    background-color: #970B67;
    font-size: 25px;
}

.jumbotron p {
    font-size: 20px;
}

/* My picture */

.my-pic {
    width: 300px;
    height: auto;
    border-radius: 50%;
    border: 5px solid rgba(34, 0, 40, 0.6);
}

/* Social Media Icons */

#my-social-media-icons {
    font-size: 40px;
}

#my-social-media-icons a {
    color: #F8EEE7;
}

#my-social-media-icons a:hover,
#my-social-media-icons a:focus,
#my-social-media-icons a:active {
    color: #f0ad4e;
    animation: pulse; 
    animation-duration: 2s; 
}

/* Media Queries */

@media (max-width: 950px) and (orientation: landscape) {
    .jumbotron {
        background-color: rgba(34, 0, 40, 0.6);
        color: #FFFFFF;
        border-radius: .3rem;
    }
    
    .jumbotron h1 {
        font-size: 35px;
    }
    
    .jumbotron h2 {
        background-color: #970B67;
        font-size: 20px;
    }
    
    .jumbotron p {
        font-size: 16px;
    }

    .my-pic {
        width: 200px;
        height: auto;
        border-radius: 50%;
        border: 5px solid rgba(34, 0, 40, 0.6);
    }
}

@media (min-width: 300px) and (max-width: 500px) {
    .jumbotron {
        background-color: rgba(34, 0, 40, 0.6);
        color: #FFFFFF;
        border-radius: .3rem;
    }
    
    .jumbotron h1 {
        font-size: 30px;
    }
    
    .jumbotron h2 {
        background-color: #970B67;
        font-size: 18px;
    }
    
    .jumbotron p {
        font-size: 14px;
    }

    .my-pic {
        width: 200px;
        height: auto;
        border-radius: 50%;
        border: 5px solid rgba(34, 0, 40, 0.6);
    }
}
#aboutSection {
    min-height: 200px;
}

.about-me-pic {
    border-radius: 10%;
    width: 450px;
    height: auto;
    border: 5px solid rgba(95, 86, 97, 0.6);
}

.about-me-pic:hover {
    transition: .5s ease;
    filter: sepia(100%);
}

/* filter: sepia(100%);} */

.skills-icons {
    width: 30px;
    height: auto;
    margin: 4px;
    padding-bottom: 16px;
}

.skills-icons:hover {
    animation: rubberBand; 
    animation-duration: 2s; 
}

/* Media Queries */

@media screen and (max-width: 900px) {
    .about-me-pic {
        border-radius: 10%;
        width: 300px;
        height: auto;
        border: 5px solid rgba(95, 86, 97, 0.6);
    }    
}
#portfolioSection {
    min-height: 500px;
}

#portfolioSection h3 {
    font-size: 35px;
}

.info-background {
    background-color: rgba(194, 160, 204, 0.3);
    padding: 15px;
    border-radius: 10px;
}

.info-background:hover {
    background-color: rgba(194, 160, 204, 0.3);
    padding: 15px;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.tech-used {
    width: 20px;
    height: auto;
    margin: 4px;
}

.link-icons {
    font-size: 25px;
    margin: 5px;
    color: #000000;
}

.link-icons:hover {
    color: #64ffda;
}

.web-image {
    margin-top: 15px;
    margin-bottom: 15px;
}

.web-image:hover {
    animation: pulse; 
    animation-duration: 2s;
}

.project-description {
    text-align: justify;
    font-size: 19px;
}

/* Media Queries */

@media screen and (max-width: 900px) {
    .web-image {
        width: 350px;
        height: auto;
    }
    .project-description {
        text-align: justify;
        font-size: 18px;
    }
}

@media screen and (max-width: 450px) {
    #portfolioSection h3 {
        font-size: 28px;
    }

    .project-description {
        text-align: justify;
        font-size: 16px;
    }
}

@media screen and (max-width: 350px) {
    #portfolioSection h3 {
        font-size: 23px;
    }
    .project-description {
        text-align: justify;
        font-size: 14px;
    }
}
.contact-img-container {
    height: 100vh;
    min-height: 800px;

    background: url(/my-portfolio/static/media/contact.8323703b.jpg) no-repeat center center fixed;
    background-size: cover;

    display: flex;
    align-items: center;
    justify-content: center;

    position: relative;
}

.contact-background {
    background-color: rgba(34, 0, 40, 0.6);
    padding-top: 15px;
    padding-bottom: 15px;
    border-radius: 20px;
}

#contactSection h2 {
    font-size: 45px;
    letter-spacing: 2px;
}

.form-control {
    background-color: #F8EEE7;
    border: 1px solid #49274A;
}

.form-control:hover,
.form-control:focus {
    background-color: #F8EEE7;
    border-color: #49274A !important;
    box-shadow: 0 0 0 0.2rem rgba(187, 71, 197, 0.445) !important;
}

.custom-button {
    background-color: #211522;
    border-color: #49274A;
    color: #FAFAFA;
}

.custom-button:hover,
.custom-button:focus {
    background-color: #970B67;
    border-color: #49274A;
    color: #FAFAFA;
}

.custom-label {
    font-size: 20px;
}

/* Media Queries */

@media screen and (max-width: 450px) {
    #contactSection h2 {
        font-size: 34px;
        letter-spacing: 2px;
    }

    .custom-label {
        font-size: 18px;
    }
}
